import React, { Component } from 'react';
import { Doughnut} from 'react-chartjs-2';
import './piediagram.css';
 class Piediagram extends React.Component {
    
     render() { 
         return ( 
            <div className="box-lg">
             <div><h4 style={{color:'#fff'}}>Total confirmed cases</h4></div>
             <div className="d-flex justify-content-center">
             <div className="pie-box">
                <Doughnut
                     data={
                        {
                            labels: ['Active', 'Recoverd', 'Deceased'],
                            datasets: [{
                              label: 'Total confirmed cases',
                              backgroundColor: [
                               'rgb(47, 130, 255)',
                               'rgb(103, 250, 44)',
                               'rgb(248, 195, 47)'
                              ],
                              hoverBackgroundColor: [
                               'rgb(36, 104, 206)',
                               'rgb(68, 163, 30)',
                               'rgb(177, 139, 35)'
                              ],
                              borderWidth:2,
                              borderColor:'rgb(51, 64, 75)',
                              data: this.props.piedata
                            }]
                          }
                     }
                     width={20}
                     height={20}
                     options={this.props.options} />
             </div>
             </div>
             <div className="mt-4">
                 <div className="d-flex justify-content-between border-color-3">
                     <div className="d-flex"><div className="square" style={{backgroundColor:'rgb(47, 130, 255)'}}></div><p>Active</p></div>
                     <div><p>{this.props.active}</p></div>
                 </div>
                 <div className="d-flex justify-content-between border-color-3">
                     <div className="d-flex"><div className="square" style={{backgroundColor:'rgb(103, 250, 44)'}}></div><p>Recoverd</p></div>
                     <div><p>{this.props.recovered}</p></div>
                 </div>
                 <div className="d-flex justify-content-between border-color-3">
                     <div className="d-flex"><div className="square" style={{backgroundColor:'rgb(248, 195, 47'}}></div><p>Deceased</p></div>
                     <div><p>{this.props.deceased}</p></div>
                 </div>
             </div>
             </div>
          );
     }
 }
  
 export default Piediagram;