import React, { Component } from 'react';
import './state.css';

class State extends React.Component {
    state = {  }
    render() { 
        return ( 
            <div className="row">
                <div className="col-12">
                    <div className="state-box">
                        <h2 className="heading_of_state">{this.props.statename}</h2>
                        <p className="update_statement">Last Updated on {this.props.dateandtime} IST</p>
                    </div>
                </div>
            </div>
         );
    }
}
 
export default State;