import React, { Component } from 'react';
import './smbox.css';
 
class Confirmed extends React.Component {
     state = {  }
     render() { 
         return ( 
            <div className="col-lg-6">
                 <div className="box-sm">
                 <h4 className="text-danger">Confirmed</h4>
                 <p className="update_statement">Updated on {this.props.date}</p>
                 <h1 className="count-color">{this.props.confirmed}</h1>
                 </div>
            </div>
          );
     }
 }
  
 export default Confirmed;