import React, { Component } from 'react';
import './globalbox.css';

class Higheststate extends React.Component {
    state = {  }
    render() { 
        return (
            <div className="col-lg-3">
                <div className="global-box">
                    <div>
                        <h4 className="text-primary">Top States</h4>
                    </div>
                    <div className="top-states">
                        <div className="d-flex justify-content-between"><div><p>1.&nbsp;{this.props.higheststate[0]}&nbsp;&nbsp;-</p></div><div>{this.props.highestconfirmed[0]}</div></div>
                        <div className="d-flex justify-content-between"><div><p>2.&nbsp;{this.props.higheststate[1]}&nbsp;&nbsp;-</p></div><div>{this.props.highestconfirmed[1]}</div></div>
                        <div className="d-flex justify-content-between"><div><p>3.&nbsp;{this.props.higheststate[2]}&nbsp;&nbsp;-</p></div><div>{this.props.highestconfirmed[2]}</div></div>
                        <div className="d-flex justify-content-between"><div><p>4.&nbsp;{this.props.higheststate[3]}&nbsp;&nbsp;-</p></div><div>{this.props.highestconfirmed[3]}</div></div>
                        <div className="d-flex justify-content-between"><div><p>5.&nbsp;{this.props.higheststate[4]}&nbsp;&nbsp;-</p></div><div>{this.props.highestconfirmed[4]}</div></div>
                       
                    </div>
                </div>
            </div>
          );
    }
}
 
export default Higheststate;