import React, { Component } from 'react';
import './globalbox.css';
import { Bar} from 'react-chartjs-2';

class Bargraph extends React.Component {
    state = { 
        options: {
            maintainAspectRatio: false,
            scales: {
                xAxes: [{
                    gridLines: {
                        display:false,
                        offsetGridLines: false
                    }
                }],
                yAxes: [{
                    gridLines: {
                        color: "rgba(0, 0, 0, 0.1)"
                    },
                    ticks: {
                        callback: function(value, index, values) {
                            return  value ;
                        },
                        beginAtZero: true
                    }
                }]
            }
        },
     }
    render() { 
        return ( 
            <div className="col-lg-6">
                <div className="global-box">
                    <div className="d-flex justify-content-between">
                        <div><h4 className="text-primary">{this.props.barlabel}</h4></div>
                        <div>
                           <div className="" style={{marginTop:'10px'}} onClick={() => {this.props.toggleDot()}}>
                             <div className="dot"></div>
                             <div className="dot"></div>
                             <div className="dot"></div>
                           </div>
                           <div className={this.props.display}>
                               <div className="classes-3" onClick={() => {this.props.getConfirmed()}}>Active</div>
                               <div className="classes-3" onClick={() => {this.props.getRecovered()}}>Recovered</div>
                               <div className="classes-3" onClick={() => {this.props.getDeceased()}}>Deceased</div>
                               <div className="classes-3" onClick={() => {this.props.exitBox()}} style={{color:'#fff',borderTop:'1px solid #0002'}}>Exit</div>
                           </div>
                        </div>
                    </div>
                    <div className="d-flex justify-content-center">
                    <div className="bar-diagram">
                        <Bar 
                         data={{
                            labels:this.props.barlabels,
                            datasets: [{
                                label: this.props.barlabel,
                                data: this.props.bardata,
                                barThickness: 15,
                                backgroundColor:this.props.barcolor ,
                                borderColor:this.props.barcolor,
                                borderWidth: 0
                              }]
                          }}
                         width={20}
                         height={20}
                         options={this.state.options}/>
                    </div>
                    </div>
                </div>
            </div>
         );
    }
}
 
export default Bargraph;