import React, { Component } from 'react';
import './sidebar.css';

import Github from './github.png';
import Home from './home.png';
import Cdn from './cdn.png';

 class Sidebar extends React.Component {
     state = {  }
     render() { 
         return ( 
             <div className="display-sidebar">
             <div className="side-bar text-center pt-2">
                <h4 style={{color:'#fff'}}>Covid</h4> 
                <p className="text-primary">India</p>
                <div> <img src={Home} width="35" className="mt-5"></img></div>
                <div><a href="https://github.com/deepmandlik"><img src={Github} width="35" className="icon2-margin"></img></a></div> 
                <div><a href="https://data.covid19india.org/v4/min/data.min.json"><img src={Cdn} width="65" className="icon3-margin "></img></a></div> 
             </div>
             </div>
          );
     }
 }
  
 export default Sidebar;