import React, { Component } from 'react';
import './globalbox.css';
 
class Guidelines extends React.Component {
    state = {  }
    render() { 
        return ( 
            <div className="col-lg-3">
                <div className="global-box">
                    <div style={{textAlign:'center'}}>
                        <h4 className="text-primary">STAY HOME, SAVE LIVES</h4>
                        <p style={{color:'grey'}}>Help to stop coronavirus</p>
                    </div>
                    <div style={{color:'#fff8'}} className="p-2">
                        <ul>
                            <li>Clean your hands often. Use soap and water.</li>
                            <li>Maintain a safe distance from anyone who is coughing.</li>
                            <li>If you have a fever, cough and difficulty breathing, seek medical attention</li>
                        </ul>
                    </div>
                </div>
            </div>
         );
    }
}
 
export default Guidelines;