import React, { Component } from 'react';
import './rate.css';

class Recovery extends React.Component {
    render() { 
        return ( 
            <div className="col-lg-12">
                <div className="rate-box">
                 <h6 style={{color:'#fff8'}}>Recovery Rate: {this.props.recoveryRate}%</h6>
                <div className="progress" style={{height:'10%',background:'#0004'}}>
                   <div className="progress-bar bg-success" style={{width:this.props.recoveryRate+'%'}}></div>
                </div><br></br>
                <h6 style={{color:'#fff8'}}>Mortality Rate: {this.props.mortalityRate}%</h6>
                <div className="progress" style={{height:'10%',background:'#0004'}}>
                   <div className="progress-bar bg-danger" style={{width:this.props.mortalityRate+'%'}}></div>
                </div>

                </div>
            </div>
         );
    }
}
 
export default Recovery;